.chat {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    height: 70px; 
    border-bottom: 1px solid #fafafa;
}

a {
    text-decoration: none;
    color: inherit;
}

.chat__details {
    flex: 1;
}

.chat__details > p {
    color: gray;
}

.chat__timestamp {
    color: lightgray;
}

.chat__image {
    margin-right: 20px;
}
@media screen and (max-width:350px){
    h2{
        font-size: 20px !important;
    }
    /* .decorate{

